module.exports = [{"title":"Chat-Cat","content":"This application is a network TCP chat server that I built as an exercise while I was at Pennapps in the Spring of 2018. The main goal of this project was to improve my skills in both network development and writing code with Go (Google's Language). The application itself consists of a multi-threaded server written in Go that accepts connections and serves the clients. The client application is written using electronjs which is a platform for writing desktop applications using web technologies (html, css, javascript). The application allows the user to have multiple conversations going on simultaneously and also group conversations with multiple parties. A feature that I did not have the chance to get to is secure communication such as session keys over which users can communicate securely. Perhaps, I will have that implemented in the application by the end of this year","link1":"https://github.com/eminkin97/Chat-Cat","link2":"https://devpost.com/software/chat-cat-3iwp1q","imgsrc":"bear.jpg"},{"title":"RU Autonomous Central Control Server","content":"Since my 1st year at Rutgers, I've been involved as a member of a team competing in the association for unmanned vehicle systems international's (AUVSI) Student Unmanned Air Systems Competition. Our team represents Rutgers and is tasked with building a drone along with an autonomous system that is able to fly to certain waypoints, detect targets on the ground, and use optical image recognition to classify those targets into certain categories. I work on the team that manages the Central Control Server through which all images taken by the drone are processed. This past year, I was made the team leader and led a team of 5 developers as we improved and maintained the system. The system consists of a manual interface which allows the user to crop pictures sent to the Control Server manually and fill in their characteristics such as description, orientation, color, etc. manually as well. Then, there is also an autonomous side on the drone itself which sends targets that already have been cropped autonomously and that have had their characteristics and attributes decided through optical image recognition software that another team works on. There is a master editing page where one can view all targets that hae been submitted and can edit/delete them as well. The server itself was written using the Django Rest framework which is a restful api and web framework written in python.","link1":"https://github.com/RUAIAA","link2":"None","imgsrc":"None"},{"title":"WingMan","content":"This application was built at the Pennapps Spring Hackathon in 2017. A two word summary of this app would be Celebrity Tinder. The team consisted of 4 developers including myself that I met at the Hackathon and based off the idea of one of the team members, we decided to undergo a deep learning classification project and after much debate, we eventually decided to do a web-based celebrity tinder. The client-side of the application is built using ReactJS as it's a single page application and React allows for dynamic rendering and real-time updates. The user is presented with an image on a web page of a celebrity and a like or a dislike. The user clicks like or dislike to indicate that he or she finds the person in the image attractive and the image changes and the process repeats. However, as the user continues to click and like or dislike certain images, the images of the celebrities begin to get more like the user's tastes. For example, if the user only liked women with red hair, then the images that would start to appear would be women with red hair. On the backend of this application, an optical face recognition library called Openface along with our stochastic gradient descent classification algorithm are working to present the user with images attuned to his/her tastes. Thus, the interface in this application may appear simple but what's happenning on the backend is anything but. The server itself is written using the Django web framework which uses python. Check out the project at the links below and let us know what you think!","link1":"https://github.com/herosang/WingMan","link2":"https://devpost.com/software/wingman-5fm728","imgsrc":"None"},{"title":"SMS-RemindMe","content":"This application is a scheduler that works via SMS text. Using the Twilio API (https://www.twilio.com/), our team of 3 rutgers students was able to build a server that is linked to a phone number which can receive SMS text messages. Then, a user can simply text a request for an appointment reminder to the number and after receiving confirmation that the text was received, the server will send a text message back to the user, at a specified time interval before the appointment, to remind the user that the appointment is happenning. For example I could text 'Pick up groceries 2pm' to the number and at 2pm i'll receive a text message indicating that I need to pick up groceries. The convenience of this application is that it works over SMS so no data charges or internet connection is necessary. The application server itself is built using the Python Flask framework. Check out the project at the links below!","link1":"https://github.com/AustinTsa1/HackRU-Submission","link2":"https://devpost.com/software/hackru-submission","imgsrc":"None"},{"title":"Classmate!","content":"This applicaion is meant to enable communication between students in college classes. The inspiration for this app was to have an application similar to GroupMe but one that every college student could join or participate in. In this spirit, this application is meant to be licensed by a school or educational institution that sets up chat rooms for all the classes that are offered at that institution. Students are, thus, free to join these chat rooms and communicate with their fellow peers to study together, work together on assignments, ask questions, etc. This app is built for the students meaning that professors are not allowed to participate in the chat room. The application itself was built for IOS using Swift and it uses a Django python Web server on the backend that the app can communicate with. Check out the project at the links below!","link1":"https://github.com/eminkin97/Classmate-","link2":"://devpost.com/software/classmate-fbnid8","imgsrc":"None"},{"title":"YouTube Chrome Extension","content":"This application is a Google Chrome Browser Extension meant to make listening to music on youtube an easier process if one is also browsing the web at the same time. Now, when the user switches to another tab in chrome (away from the youtube video) a small dropdown appears that enables the user to pause, mute, skip, or move the scrubber in a youtube video. The extension handles multiple tabs so if the user has two youtube videos in two different tabs, then the extension will have two tabs as well. This extension makes listening to music and videos on youtube a much more user-friendly experience. The application is built with web technologies (javascript, html, css, bootstrap). Check it out at the Link below!","link1":"https://github.com/davidt314159/YouTube-Player-Chrome-Extension","link2":"None","imgsrc":"None"},{"title":"Simple OS dev","content":"This application is the culmination of my work in cs416: Operating Systems at Rutgers university. This project contains a threading library implemented in the C programming library which models the functions and scheduler used in the pthread.h library. The part of this project contains a dynamic memory allocator that handles memory for a multi-threaded process. It does this by allocating page nodes in memory for each of these threads and continually swapping the page nodes out when a new thread is swapped into execution. The final part of this project contains a simple file system implemented (also in the C programming language) with the fuse library which reroutes system calls to functions that I wrote. The project consists of creating the main linux terminal commands (cd, ls, mkdir, rmdir, rm, touch, etc.) and allocating space for newly created files through the use of inodes in a flatfile that is treated as memory. Check out this enormous project at the link below!","link1":"https://github.com/eminkin97/Simple-os-dev-projs","link2":"None","imgsrc":"None"},{"title":"This Site!","content":"This site was built entirely by me from the ground up using web technologies (html, css) and the vuejs web framework (https://vuejs.org/). It took me a fair amount of time so I hope you like it!","link1":"None","link2":"None","imgsrc":"None"}]

