module.exports = [{"title":"The Beginning","imgsrc":"bear.jpg","date":"May 12th, 2018","content":"Hi! Welcome to the Blog part of this site. To be honest, I'm not really sure why I even added a blog ... i've never really been a blogger ... but i've built it and it's already here so I might as well use it. I'm not really sure what I'm going to write about yet so bear (Ha ha! that's why I put in the bear photo ... lol get it) with me please. I apologize in advance for all the spelling errors. Anyway, hope you enjoy the site!"},{"title":"Entry 1: The Code Book","imgsrc":"cryptomeme.png","date":"May 13th, 2018","content":"For the first post, I think I'm going to briefly discuss what I thought of The Code Book by Simon Singh. I read the codebook this past March as a suggested reading to supplement a cryptography class I was taking and, compared to all the other non-fiction I've read which usually puts me to slip, I think that Singh managed to make the history of secure communication interesting, suspenseful, and actually fun to read. It starts with the origin of the use of secure communication and the first known methods for communicating securely. One of the interesting one's I thought was shaving the messenger's head, writing the message on his skull, and then waiting for the hair to grow back; then, the messenger can go along and deliver the message by shaving his head again and guards would never think to look under his hair. That one is definitely the coolest/craziest way of communicating securely but a more pivotal method is called the Caesar Cipher which was employed in Julius Caesar's time. Although this cipher was simple enough (it consists of just shifting every letter in the passage by a fixed number 1-26), it served Caesar well in his time since people weren't very mathematically inclined and when they saw the seemingly nonsensical combination of letters, they had no idea what it could possible mean. From then on, people became aware of the incredible usefulness that cryptography could play and it became an essential tool for military communication. Singh progresses through the book talking about many more ciphers including the substitution cipher (substituting each letter in the alphabet by another one), the Vignere cipher (a polyalphebetic substitution cipher), the Enigma cipher, etc. and as well talks about other applications of cryptography including the decipherment of ancient languages such as Egyptian hieroglyphics and Linear B. I actually wrote my final paper for the class on the former (got an A nbd). There is a long section on the cracking of the Enigma cipher during World War II by the cryptogtaphy team at Bletchley Park and its importance to the Allied forces victory. Thought this part was particularly interesting since I've seen the movie 'The Imitation Game' and it turns out that historically things happenned a bit differently. Lastly, the final section of the book talks briefly about modern cryptographic schemes such as AES and DES and RSA. He also discusses the future of quantum cryptanalysis and cryptography and how the invention of quantum computers is bound to change secure communication as we know it. Overall, it's a great read for anyone who's interested in the subject and doesn't contain too much math or fancy language making it relatively easy to understand, even for someone who's not from a technical background. Best nonfiction book I've read hands down. It actually inspired me to read another one of Singh's works about the Big Bang (even though I really hate physics). Hopefully, that one ends up being interesting as well"}]

