<template>
  <div id="container">
	<h1>{{ title }}</h1>
	<div>
		<p>{{ content }}</p>
	</div>
	<a v-if="link1_status" v-bind:href="link1">Check out the project on Github!</a>
	<a v-if="link2_status" v-bind:href="link2">Check out the project on Devpost!</a>
  </div>
</template>

<script>
export default {
  name: 'Project',
  props: ['title', 'content', 'link1', 'link2', 'imgsrc'],
  data() {
	return {
		link1_status: false,
		link2_status: false,
	}
  },
  mounted: function() {
	if (this.link1 != "None") {
		this.link1_status = true;
	}
	if (this.link2 != "None") {
		this.link2_status = true;
	}
  }
}
</script>

<style scoped>
	#container {
		border: 1px solid gray;
		margin: 20px 100px;
		border-radius: 50px;
		background: white;
	}
	p {
		margin: 5px 200px;	
		font-family: Verdana, Geneva, sans-serif;
	}
	h1 {
		font-family: Verdana, Geneva, sans-serif;
	}
</style>



