<template>
  <div>
	<Project
	   v-for="data in projects"
	   v-bind:title="data.title"
	   v-bind:content="data.content"
	   v-bind:link1="data.link1"
	   v-bind:link2="data.link2"
	   v-bind:imgsrc="data.imgsrc">
	</Project>
  </div>
</template>

<script>
import Project from './Project.vue';
import json from '../assets/json/projects.json';

export default {
  name: 'Projects',
  components: {
	Project
  },
  data () {
	return {
		projects: json
	}
  }
}
</script>

<style scoped>
</style>



