<template>
  <div id="container">
	<h1>{{ title }}</h1>
	<p id="date"> {{ date }} </p>
	<img :src="'/static/' + imgsrc"></img>
	<p id="content">{{ content }}</p>
  </div>
</template>

<script>
export default {
  name: 'Blogentry',
  props: ['title', 'date', 'content', 'imgsrc']
}
</script>

<style scoped>
	#container {
		text-align: center;
		border-bottom: 2px solid gray;
	}
	h1, p {
		font-family: Verdana, Geneva, sans-serif;
	}
	img {
		width: 250px;
		height: 250px;
	}

	#date {
		margin-top: 0px;
	}

	#content {
		margin: 20px 180px;
	}
</style>



