<template>
  <div id="content">
	<div id="header">
		<h1 v-on:click="redirectToHome">Eli Minkin's site</h1>

		<div id="search">
			<form method="get" action="http://www.google.com/search">
				<input type="text" name="q">
				<input type="submit" name="Submit" value="Search">
			</form>
			<span v-on:click="scrollToBottom">Contact</span>
		</div>
	</div>
	<div id="inner_content">
		<Navbar></Navbar>
		<router-view id="view"></router-view>
	</div>
	<div id="footer">
		<p>Website created by Eli Minkin</p>
		<p>For all questions, please contact Eli Minkin at eminkin99@gmail.com</p>
		<p>Last Updated: 5/11/18</p>
	</div>
  </div>
</template>

<script>
import Navbar from './Navbar.vue';

export default {
  name: 'Content',
  components: {
	Navbar
  },
  methods: {
	scrollToBottom: function(e) {
		//scroll to bottom of page
		window.scrollTo({
			left: 0,
			top: document.body.scrollHeight,
			behavior: 'smooth'
		});
	},
	redirectToHome: function(e) {
		document.location.href="/";
	}
  }
}

</script>

<style scoped>
	#content {
		height: 100%;
		width: 100%;
		text-align: center;
	}
	#inner_content {
		width: 100%;
		display: inline-block;
	}
	#view {
		border: 1px solid black;
		background: linear-gradient(to top, #ffcc66 0%, #ffeecc 100%);
	}
	#header {
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 100%;
		height: 80px;
		border-bottom: 1px solid black;
		background-color: #FF7D9D;
		background-size: 58px 58px;
		background-position: 0px 2px, 4px 35px, 29px 31px, 33px 6px,
		0px 36px, 4px 2px, 29px 6px, 33px 30px;
		background-image: 
		linear-gradient(335deg, #C90032 23px, transparent 23px),
		linear-gradient(155deg, #C90032 23px, transparent 23px),
		linear-gradient(335deg, #C90032 23px, transparent 23px),
		linear-gradient(155deg, #C90032 23px, transparent 23px),

		linear-gradient(335deg, #C90032 10px, transparent 10px),
		linear-gradient(155deg, #C90032 10px, transparent 10px),
		linear-gradient(335deg, #C90032 10px, transparent 10px),
		linear-gradient(155deg, #C90032 10px, transparent 10px);
	}

	#header > h1 {
		margin: 0;
		color: white;
		cursor: pointer;
		font-family: Arial Black;
		border: 5px dotted blue;
		border-radius: 20px;
		width: 300px;
	}

	#search {
		text-align: left;
	}

	#search > span {
		font-family: Verdana, Geneva, sans-serif;
		color: white;
		font-size: 12px;
		font-weight: bolder;
		cursor: pointer;
	}
	#search > span:hover {
		text-decoration: underline;
	}

	#footer {
		width: 100%;
		height: 80px;
		text-align: center;
		border-top: 1px solid black;
		background-color: #FF7D9D;
		background-size: 58px 58px;
		background-position: 0px 2px, 4px 35px, 29px 31px, 33px 6px,
		0px 36px, 4px 2px, 29px 6px, 33px 30px;
		background-image: 
		linear-gradient(335deg, #C90032 23px, transparent 23px),
		linear-gradient(155deg, #C90032 23px, transparent 23px),
		linear-gradient(335deg, #C90032 23px, transparent 23px),
		linear-gradient(155deg, #C90032 23px, transparent 23px),

		linear-gradient(335deg, #C90032 10px, transparent 10px),
		linear-gradient(155deg, #C90032 10px, transparent 10px),
		linear-gradient(335deg, #C90032 10px, transparent 10px),
		linear-gradient(155deg, #C90032 10px, transparent 10px);
	}

	#footer > p {
		background: gray;
		font-size: 12px;
		color: white;;
		font-weight: bold;
		margin: 5px;
	}
</style>



