<template>
  <div>
	<Blogentry
	   v-for="data in blogentries"
	   v-bind:title="data.title"
	   v-bind:date="data.date"
	   v-bind:content="data.content"
	   v-bind:imgsrc="data.imgsrc">
	</Blogentry>

  </div>
</template>

<script>
import json from '../assets/json/blog.json';
import Blogentry from './Blogentry.vue'

export default {
  name: 'Blog',
  data () {
	return {
		blogentries: json.reverse()
	}
  },
  components: {
	Blogentry
  }

}
</script>

<style scoped>
</style>



